<template>
    <v-container>
        <h2>Elige tu foto de perfil</h2>
        <v-flex xs12 lg6 offset-lg3>
            <v-container>
                    <v-layout>
                        <v-flex xs4 offset-xs4>
                            <vuetify-lazy-image
                                    :src="imagePreview"
                                    :aspect-ratio="1"
                            ></vuetify-lazy-image>
                            <v-container>
                                <v-btn
                                        absolute
                                        dark
                                        fab
                                        right
                                        color="success"
                                        @click="$refs.myFiles.click()"
                                >
                                    <v-icon>mdi-camera</v-icon>
                                </v-btn>

                            </v-container>

                        </v-flex>
                    </v-layout>

                    <input hidden ref="myFiles" @change="previewFiles" type="file" accept="image/*" capture>
            </v-container>
            <!--Add Button-->

            <!--SnackBar-->
            <v-snackbar
                    v-model="snackbar"
                    :timeout="2500"
                    :top="true"
            >
                {{ textSnackbar }}
                <v-btn
                        color="pink"
                        flat
                        @click="snackbar = false"
                >
                    Close
                </v-btn>
            </v-snackbar>
            <!--End SnackBar-->
            <loading v-if="formSending"></loading>
        </v-flex>
    </v-container>

</template>

<script>
    import Vue from 'vue'
    import firebase from 'firebase'
    import imageCompression from 'browser-image-compression'

    const defaultImage = '../assets/logo.png'

    export default {
        name: "SetYourProfileImage",
        data: () => ({
            formSending: false,
            valid: true,
            imagePreview: defaultImage,
            imageUpload: defaultImage.replace("data:image/jpeg;base64,", "").replace("data:image/png;base64,", ""),
            name: '',
            nameRules: [
                v => !!v || 'Se requiere un nombre.'
            ],
            lastName: '',
            lastNameRules: [
                v => !!v || 'Se require un apellido.'
            ],
            genre: 'm',
            optionsGenre: [
                {value: 'm', text: 'Hombre'},
                {value: 'h', text: 'Mujer'}
            ],
            birthday: new Date().toISOString().substr(0, 10),
            menu: false,
            modal: false,
            menu2: false,

            diet: 'Sin Dieta',
            optionsDiet: [
                {value: 'Sin Dieta', text: 'Sin Dieta'},
                {value: 'Dieta hipercalórica', text: 'Dieta hipercalórica'},
                {value: 'Dieta hiperprotéica', text: 'Dieta hiperprotéica'},
                {value: 'Otra/Suplementos', text: 'Otra/Suplementos'}
            ],

            centerId: '',
            centers: [],

            snackbar: false,
            textSnackbar: 'Error'
        }),
        methods:{
            previewFiles() {
                if (this.$refs.myFiles.files.length > 0) {
                    if (!this.$refs.myFiles.files[0].type.includes('image/')) {
                        alert('Please select an image file')
                        return
                    }

                    let imageFile = this.$refs.myFiles.files[0];

                    let options = {
                        maxSizeMB: 0.5,
                        maxWidthOrHeight: 500,
                        useWebWorker: true
                    }

                    imageCompression(imageFile, options)
                        .then( (compressedFile) => {
                            if (typeof FileReader === 'function') {
                                const reader = new FileReader()
                                reader.onload = (event) => {
                                    this.imgSrc = event.target.result
                                    this.imagePreview = this.imgSrc
                                    this.imageUpload = this.imagePreview.replace("data:image/jpeg;base64,", "").replace("data:image/png;base64,", "");
                                }
                                reader.readAsDataURL(compressedFile)
                            } else {
                                alert('Sorry, FileReader API not supported')
                            }

                        })
                        .catch(function (error) {
                            console.log(error.message);
                        });
                }
            },
            goBack() {
                this.$router.push({name: 'patientHome'})
            },
            create() {
                if (this.formSending) {
                    return
                }
                this.formSending = true
                this.$refs.form.validate()
                Vue.nextTick(() => {
                    if (this.valid) {
                        let db = firebase.firestore()
                        db.collection("patients").add({
                            name: this.name,
                            lastName: this.lastName,
                            genre: this.genre,
                            birthday: this.birthday,
                            diet: this.diet,
                            centerId:this.centerId,
                            wheelChairName: 'Sin silla',
                            wheelChairWeight: 0
                        })
                            .then((patient) => {
                                let storageRef = firebase.storage().ref().child("userImages/" + patient.id);
                                storageRef.putString(this.imageUpload, 'base64')
                                    .then(() => {
                                        console.log('Uploaded a base64 string!');
                                        storageRef.getDownloadURL().then((url) => {
                                            patient.update({
                                                imageUrl: url,
                                            })
                                                .then(() => {
                                                    this.$router.push({name: 'patientHome'})
                                                    this.formSending = false
                                                })
                                                .catch(() => {
                                                    this.snackbar = true
                                                    this.textSnackbar = 'Error creando el centro'
                                                    this.formSending = false
                                                })
                                        })
                                    })
                                    .catch(() => {
                                        this.snackbar = true
                                        this.textSnackbar = 'Error subiendo la imagen'
                                        this.formSending = false
                                    })
                            })
                            .catch(() => {
                                this.snackbar = true
                                this.textSnackbar = 'Error creando el paciente'
                                this.formSending = false
                            })

                    } else {
                        this.formSending = false
                    }
                })
            },
        }
    }
</script>

