<template>
    <v-container class="email-verification">
        <h2>Verifica tu E-mail</h2>
        <p style="text-align:justify">
            Para poder continuar, completa el proceso de verificación a través del enlace que hemos enviado a tu E-mail.
        </p>

        <br>

        <div align="center">
            <v-progress-circular
                    :size="50"
                    color="primary"
                    indeterminate
            ></v-progress-circular>
        </div>

        <!-- Error snackbar -->
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                Cerrar
            </v-btn>
        </v-snackbar>

        <br><br>
    </v-container>
</template>

<script>
    import * as firebase from "firebase";

    export default {
        name: "emailVerification",
        data(){
            return {
                sendEmailButtonIsDisabled: true,
                snackbar: false,
                snackbarText:'',
                isEmailVerified: false,
                intervalCheckEmailVerified: null,
                user: null
            }
        },
        methods:{
            sendEmailVerification: function (){
                //Send verification email
                this.user.sendEmailVerification().then(function () {
                    console.log('verification sent: ');
                }, function(error) {
                    console.log('verification not sent: ' + error.message);
                });
            },
            checkEmailVerified: function () {
                console.log('running')
                this.user.reload().then(() => {
                    if(firebase.auth().currentUser.emailVerified){
                        console.log("Email is verified");
                        clearInterval(this.intervalCheckEmailVerified);
                        this.$router.replace('fill-your-profile-info');
                    }
                });
            }
        },
        beforeMount(){
            this.user = firebase.auth().currentUser;
            console.log(this.user.emailVerified)
            if(!this.user.emailVerified){
                this.sendEmailVerification();
            }
            this.intervalCheckEmailVerified = setInterval(this.checkEmailVerified,1000)
        },
    }
</script>


