<template>
    <v-container class="fill-your-profile-info">
        <h2>Rellena tu perfil</h2>
        <v-form v-model="validForm">
            <v-text-field
                    v-model="name"
                    :rules="[rules.required]"
                    label="Nombre"
                    required
            ></v-text-field>

            <v-text-field
                    v-model="lastName"
                    :rules="[rules.required]"
                    label="Apellidos"
                    required
            ></v-text-field>

            <v-dialog
                    ref="dialog"
                    v-model="modal"
                    :return-value.sync="birthDate"
                    persistent
            >
                <template v-slot:activator="{ on }">
                    <v-text-field
                            v-model="birthDate"
                            label="Fecha de nacimiento"
                            hint="AAAA-MM-DD"
                            append-icon="mdi-calendar"
                            :rules="[rules.required, rules.minAge]"
                            readonly
                            v-on="on"
                            required
                    ></v-text-field>
                </template>
                <v-date-picker v-model="birthDate" scrollable width="97%" locale="es">
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
                    <v-btn text color="primary" @click="$refs.dialog.save(birthDate)">OK</v-btn>
                </v-date-picker>
            </v-dialog>


            <v-textarea
                    outlined
                    v-model="description"
                    :rules="[rules.required, rules.min20Char]"
                    label="Cuéntanos un poco sobre ti"
                    required
                    counter
                    maxlength="140"
            ></v-textarea>
        </v-form>
        <br>
        <div align="center">
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="setProfileInfo" :disabled="buttonIsDisabled">Continuar</v-btn>
            <v-spacer></v-spacer>
        </div>
        <br>

        <!-- Error snackbar -->
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                Cerrar
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import * as firebase from "firebase";

    export default {
        name: "fillYourProfileInfo",
        data(){
            return {
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText:'',
                name:'',
                lastName:'',
                birthDate: null,
                menu: false,
                modal: false,
                description:'',
                validForm:false,
                rules: {
                    required: value => !!value || 'Este campo es obligatorio',
                    min20Char: value => value.length >= 20 || 'Mínimo 20 caracteres',
                    minAge: value => {
                        const bDate = new Date(value);
                        const currentDate = new Date();
                        let validAge;
                        if(currentDate.getFullYear() - bDate.getFullYear() < 16){
                            validAge = false;
                        }
                        else if(currentDate.getFullYear() - bDate.getFullYear() == 16){
                            if(currentDate.getMonth() < bDate.getMonth()){
                                validAge = false;
                            }
                            else if(currentDate.getMonth() == bDate.getMonth()){
                                if(currentDate.getDate() < bDate.getMonth()){
                                    validAge = false;
                                }
                                else{
                                    validAge = true;
                                }
                            }
                            else{
                                validAge = true;
                            }
                        }
                        else{
                            validAge = true;
                        }

                        return validAge || 'Debes tener al menos 16 años para poder registrarte';
                    },
                    samePassword: value => value.localeCompare(this.password) == 0 || 'Las contraseñas no coinciden',
                }
            }
        },
        methods:{
            setProfileInfo: function () {
                let firestoreDB = firebase.firestore();
                console.log(firebase.auth().currentUser.uid)
                firestoreDB.collection("users").doc(firebase.auth().currentUser.uid).set(
                    {
                        name: this.name,
                        lastName: this.lastName,
                        birthDate: this.birthDate,
                        description: this.description
                    },
                    { merge: true }
                )
                .then(() => {
                    console.log("Profile info successfully written in users");
                    this.$router.replace('fill-your-languages');
                })
                .catch(function (error) {
                    console.error("Error writing profile info in users: ", error);
                    this.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                    this.snackbar = true
                    return
                });
            },
        },
        watch: {
            validForm: function (value) {
                if(value){
                    this.buttonIsDisabled = false
                }
                else{
                    this.buttonIsDisabled = true
                }
            }
        }
    }
</script>

