import Vue from 'vue'
import Router from 'vue-router'
import firebase from "firebase";
import Home from './views/Home.vue'
import Login from "./views/Login";
import SignUp from "./views/SignUp";
import EmailVerification from "./views/EmailVerification";
import FillYourProfileInfo from "./views/FillYourProfileInfo";
import FillYourLanguages from "./views/FillYourLanguages";
import SetYourProfileImage from "./views/SetYourProfileImage";
import EmailChangeSentConfirmation from "./views/EmailChangeSentConfirmation";

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      //To redirect all routes that do not exist to /home
      path: '*',
      redirect: '/home'
    },
    {
      //To redirect root route to /home
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path:'/login',
      name:'login',
      component: Login
    },
    {
      path:'/sign-up',
      name: 'signUp',
      component: SignUp
    },
    {
      path:'/email-verification',
      name: 'emailVerification',
      component: EmailVerification,
      meta: {
        requiresAuth: true
      }
    },{
      path:'/fill-your-profile-info',
      name: 'fillYourProfileInfo',
      component: FillYourProfileInfo,
      meta: {
        requiresAuth: true
      }
    },{
      path:'/fill-your-languages',
      name: 'fillYourLanguages',
      component: FillYourLanguages,
      meta: {
        requiresAuth: true
      }
    }, {
      path: '/set-your-profile-image',
      name: 'setYourProfileImage',
      component: SetYourProfileImage,
      meta: {
        requiresAuth: true
      }
    },{
      path:'/email-change-sent-confirmation',
      name: 'emailChangeSentConfirmation',
      component: EmailChangeSentConfirmation,
      meta: {
        requiresAuth: false
      }
    }
  ]
});

router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if(requiresAuth && !currentUser){
    next('home');
  }
  else{
    next();
  }
});

export default router;
