<template>
    <v-container class="fill-your-languages">
        <h2>Tus idiomas</h2>
        <div class="languages_container">
            <h4>Idiomas maternos</h4>
                <v-combobox
                        v-model="chips_known_languages"
                        :items="items_known_languages"
                        chips
                        clearable
                        label="¿Qué idiomas hablas con soltura?"
                        multiple
                        solo
                >
                    <template v-slot:selection="{ attrs, item, select, selected }">
                        <v-chip
                                v-bind="attrs"
                                :input-value="selected"
                                close
                                @click="select"
                                @click:close="remove_known_language(item)"
                        >
                            <img
                                    left
                                    src = "languages[2].icon"
                            >
                            <strong>{{ item.text }}</strong>&nbsp;
                        </v-chip>
                    </template>
            </v-combobox>
        </div>

        <div class="languages_container">
            <h4>Idiomas que quieres practicar</h4>
            <v-combobox
                    v-model="chips_languages_to_learn"
                    :items="items_languages_to_learn"
                    chips
                    clearable
                    label="¿Qué idiomas buscas mejorar?"
                    multiple
                    solo
            >
                <template v-slot:selection="{ attrs, item, select, selected }">
                    <v-chip
                            v-bind="attrs"
                            :input-value="selected"
                            close
                            @click="select"
                            @click:close="remove_language_to_learn(item)"
                    >
                        <strong>{{ item.text }}</strong>&nbsp;
                    </v-chip>
                </template>
            </v-combobox>
        </div>
            <div align="center">
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="setLanguages" :disabled="buttonIsDisabled">Continuar</v-btn>
                <v-spacer></v-spacer>
            </div>
            <br>

            <!-- Error snackbar -->
            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="blue"
                        text
                        @click="snackbar = false"
                >
                    Cerrar
                </v-btn>
            </v-snackbar>

    </v-container>
</template>

<script>
    const languages = [
        {
            text: 'Alemán',
            icon: '../assets/flags/de.svg'
        },
        {
            text: 'Chino',
            icon: '../assets/flags/zh.svg'
        },{
            text: 'Español',
            icon: 'es.png'
        },
        {
            text: 'Francés',
            icon: '../assets/flags/fr.svg'
        },
        {
            text: 'Inglés',
            icon: '../assets/flags/en.svg'
        },
        {
            text: 'Italiano',
            icon: '../assets/flags/it.svg'
        },
        {
            text: 'Japonés',
            icon: '../assets/flags/ja.svg'
        },
        {
            text: 'Portugués',
            icon: '../assets/flags/pt.svg'
        }
    ];

    import * as firebase from "firebase";

    export default {
        name: "FillYourLanguages",
        data () {
            return {
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText:'',
                chips_known_languages: [],
                chips_languages_to_learn: [],
                items_known_languages: languages,
                items_languages_to_learn: languages,
            }
        },

        methods: {
            remove_known_language (item) {
                this.chips_known_languages.splice(this.chips_known_languages.indexOf(item), 1)
                this.chips_known_languages = [...this.chips_known_languages]
            },
            remove_language_to_learn (item) {
                this.chips_languages_to_learn.splice(this.chips_languages_to_learn.indexOf(item), 1)
                this.chips_languages_to_learn = [...this.chips_languages_to_learn]
            },
            validLanguages() {
                if (this.chips_known_languages.length > 0 && this.chips_languages_to_learn.length > 0) {
                    this.buttonIsDisabled = false;
                } else {
                    this.buttonIsDisabled = true;
                }
            },
            filter_items_known_languages: function(){
                // eslint-disable-next-line no-unused-vars
                let self = this;
                this.items_known_languages = languages.filter(function (value, index, arr) {
                    return !self.chips_languages_to_learn.includes(value);
                })
            },
            filter_items_languages_to_learn: function(){
                // eslint-disable-next-line no-unused-vars
                let self = this;
                this.items_languages_to_learn = languages.filter(function (value, index, arr) {
                    return !self.chips_known_languages.includes(value);
                })
            },
            setLanguages(){
                let firestoreDB = firebase.firestore();

                let self = this;

                firestoreDB.collection("users").doc(firebase.auth().currentUser.uid).set(
                    {
                        known_languages: this.chips_known_languages,
                        languages_to_learn: this.chips_languages_to_learn
                    },
                    { merge: true }
                )
                    .then(() => {
                        console.log("Languages info successfully written in users");
                        this.$router.replace('set-your-profile-image');
                    })
                    .catch(function (error) {
                        console.error("Error writing languages info in users: ", error);
                        self.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                        self.snackbar = true;
                        return
                    });
            }
        },
        watch: {
            chips_known_languages: function(){
                this.validLanguages();
                this.filter_items_languages_to_learn();
            } ,
            chips_languages_to_learn: function(){
                this.validLanguages();
                this.filter_items_known_languages();
            },

        }
    }
</script>

<style scoped>
    .languages_container{
        height: 40%;
    }

    .fill-your-languages{
        height: 100%;
        position: relative;
        width: 100%
    }
</style>