<template>
    <v-container class="sign-up">
        <h2>Crea tu cuenta</h2>
        <v-form v-model="validForm">
            <v-text-field
                    v-model="username"
                    :rules="[rules.required, rules.min4Char, rules.validUsername]"
                    label="Nombre de usuario"
                    counter
                    required
            ></v-text-field>

            <v-text-field
                    v-model="email"
                    :rules="[rules.required, rules.email]"
                    label="E-mail"
                    required
            ></v-text-field>

            <v-text-field
                    v-model="password"
                    :rules="[rules.required, rules.min8Char, rules.samePassword]"
                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="showPassword ? 'text' : 'password'"
                    label="Contraseña"
                    hint="Mínimo 8 caracteres"
                    counter
                    @click:append="showPassword = !showPassword"
                    required
            ></v-text-field>

            <v-text-field
                    v-model="password2"
                    :rules="[rules.required, rules.min8Char, rules.samePassword]"
                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="showPassword ? 'text' : 'password'"
                    label="Repite la contraseña"
                    hint="Mínimo 8 caracteres"
                    counter
                    @click:append="showPassword = !showPassword"
                    required
            ></v-text-field>
        </v-form>
        <br>
        <div align="center">
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="signUp" :disabled="buttonIsDisabled">Registrar</v-btn>
            <v-spacer></v-spacer>
        </div>
        <br>
        <div align="center">
            <v-spacer></v-spacer>
            <p>¿Ya tienes una cuenta? <router-link to="/login">¡Accede ahora!</router-link></p>
            <v-spacer></v-spacer>
        </div>

        <!-- Error snackbar -->
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                Cerrar
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import * as firebase from "firebase";

    export default {
        name: "signUp",
        data(){
            return {
                validForm: false,
                username:'',
                email:'',
                password:'',
                password2:'',
                showPassword: false,
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText:'',
                rules: {
                    required: value => !!value || 'Este campo es obligatorio',
                    min4Char: value => value.length >= 4 || 'Mínimo 4 caracteres',
                    min8Char: value => value.length >= 8 || 'Mínimo 8 caracteres',
                    email: value => {
                        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                        return pattern.test(value) || 'E-mail no válido'
                    },
                    validUsername: value => {
                        const pattern1 = /[a-zA-Z_]+[a-zA-Z0-9_]*/
                        const pattern2 = /^_/
                        const pattern3 = /_[_]+/
                        return (pattern1.test(value) && !pattern2.test(value) && !pattern3.test(value)) || 'Nombre de usuario no válido'
                    },
                    samePassword: value => value.localeCompare(this.password) == 0 || 'Las contraseñas no coinciden',
                },
            }
        },
        methods:{
            signUp: async function () {
                let firestoreDB = firebase.firestore();

                // Is username registered?
                if ( await this.isUsernameInUse()) {
                    console.log('entro')
                    this.snackbarText = 'Ya existe un usuario con ese nombre';
                    this.snackbar = true;
                    return;
                }

                //Create firebase user
                firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then(() => {
                    firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL)
                        .then(() => {
                           firestoreDB.collection("users").doc(firebase.auth().currentUser.uid).set(
                                {
                                    username: this.username
                                }
                            )
                            .then(() => {
                                console.log("Username successfully written in users");
                                this.$router.replace('email-verification');
                            })
                            .catch(function (error) {
                                console.error("Error writing username in users: ", error);
                                this.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                                this.snackbar = true
                                return
                            });

                        })
                        .catch((error) => {
                            console.error(error)
                            this.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                            this.snackbar = true
                            return
                        });
                })
                    .catch((error) => {
                        console.error(error)

                        if (error.code == 'auth/email-already-in-use') {
                            this.snackbarText = 'Ya existe una cuenta registrada con ese E-mail'
                        } else {
                            this.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                        }
                        this.snackbar = true
                        return
                    });
            },

            isUsernameInUse: async function () {
                    let firestoreDB = firebase.firestore();

                    // Is username registered?
                    let usernameAlreadyExists = false;
                    let [querySnapshot] = await Promise.all([firestoreDB.collection("users").where("username", "==", this.username).get()]);

                    if (querySnapshot.size > 0) {
                        usernameAlreadyExists = true;
                    }

                    return usernameAlreadyExists;
            }
        },

        watch: {
            validForm: function (value) {
                if(value){
                    this.buttonIsDisabled = false
                }
                else{
                    this.buttonIsDisabled = true
                }
            }
        }
    }
</script>

